<template>
  <div>
    <!-- 顶部 -->
    <van-swipe @change="onChange" class="vanSwipe">
      <van-swipe-item>
        <img
          src="https://goods2.juancdn.com/goods/190924/3/0/5d89ba9633b089350b458642_800x800.jpg?imageMogr2/quality/88!/format/jpg"
          alt
        />
      </van-swipe-item>
      <van-swipe-item>
        <img
          src="https://goods8.juancdn.com/goods/190924/f/c/5d89ba9d33b089688f68b50b_800x800.jpg?imageMogr2/quality/88!/format/jpg"
          alt
        />
      </van-swipe-item>
      <van-swipe-item>
        <img
          src="https://goods3.juancdn.com/goods/190924/4/4/5d89baa6b6f8ea68a236f1be_800x800.jpg?iopcmd=convert&Q=88&dst=jpg"
          alt
        />
      </van-swipe-item>
      <van-swipe-item>
        <img
          src="https://goods1.juancdn.com/goods/190924/1/0/5d89baab33b08978192b9455_800x800.jpg?iopcmd=convert&Q=88&dst=jpg"
          alt
        />
      </van-swipe-item>
      <div class="custom-indicator" slot="indicator">{{ current + 1 }}/4</div>
    </van-swipe>

    <!-- 满减 -->
    <div class="imgbo">
      <img
        src="https://goods8.juancdn.com/jas/200301/f/5/5e5b74feb6f8ea409a7c8021_1080x144.png?imageMogr2/quality/88!/format/png"
        alt
      />
    </div>
    <!-- 价格包邮部分 -->
    <div class="title">
      <div class="title-top">
        <!-- 左边logo -->
        <span class="left-logo">
          <img
            src="https://goods2.juancdn.com/jas/200301/3/9/5e5b711533b089390f730f80_126x45.png?imageMogr2/quality/88!/format/png"
            alt
          />
        </span>
        <!-- 标题部分 -->
        <span style="font-size:14px">￥</span>
        <span class="price1">18.8</span>
        <span class="joyous">包邮</span>
        <span class="num">1780人已购</span>
      </div>
      <div class="title-text">
        <p class="describe">创意不锈钢便携式餐具套装学生筷子长柄勺子叉子餐具盒旅行套装</p>
        <button
          @click="active"
          :class="{btn:true,active:$store.state.classify.Single[$route.query.index].active}"
        ></button>
      </div>
    </div>

    <!-- 单元格 -->
    <van-cell center>
      <span class="mj">满减</span>
      <span class="sale">满139元减70元,上不封顶</span>
      <van-icon
        slot="right-icon"
        name="https://web.juanpi.com/static/media/more.66830346.png"
        class="custom-icon img-icon"
      />
    </van-cell>

    <!-- 配置 -->
    <van-cell center>
      <span class="ensure">24小时发货</span>
      <span class="ensure">7天包退</span>
      <span class="ensure">售后补贴</span>
      <van-icon
        slot="right-icon"
        name="https://web.juanpi.com/static/media/more.66830346.png"
        class="custom-icon img-icon"
      />
    </van-cell>

    <van-cell title="请选择：颜色、尺码" is-link @click="showBase = true" />

    <!-- 弹出购买选项 -->
    <div class="sku-container">
      <van-sku
        v-model="showBase"
        :sku="skuData.sku"
        :goods="skuData.goods_info"
        :goods-id="skuData.goods_id"
        :hide-stock="skuData.sku.hide_stock"
        :quota="skuData.quota"
        :quota-used="skuData.quota_used"
        :initial-sku="initialSku"
        reset-stepper-on-hide
        reset-selected-sku-on-hide
        disable-stepper-input
        :close-on-click-overlay="closeOnClickOverlay"
        :custom-sku-validator="customSkuValidator"
        @buy-clicked="onClickButton"
        @add-cart="onAddCartClicked"
      />
    </div>

    <van-cell
      center
      title="四季家居厨房上新专场"
      value="进入专场"
      label="34.6万人已购"
      icon="https://goods3.juancdn.com/seller/171224/4/a/5a3fbea78150a166b53e7a65_180x90.png?iopcmd=convert&Q=88&dst=png"
    ></van-cell>

    <div class="swiper-container swiperHomeNav">
      <div class="swiper-wrapper">
        <div class="swiper-slide">
          <img
            src="https://goods2.juancdn.com/goods/171027/3/a/59f34541a9fcf819a6442aff_800x800.jpg?imageMogr2/thumbnail/350x350!"
            alt
          />
        </div>
        <div class="swiper-slide">
          <img
            src="https://goods4.juancdn.com/goods/190413/7/4/5cb17b0a33b0891b5749ee47_800x800.jpg?imageMogr2/thumbnail/350x350!"
            alt
          />
        </div>
        <div class="swiper-slide">
          <img
            src="https://goods2.juancdn.com/goods/171027/3/a/59f34541a9fcf819a6442aff_800x800.jpg?imageMogr2/thumbnail/350x350!"
            alt
          />
        </div>
        <div class="swiper-slide">
          <img
            src="https://goods2.juancdn.com/goods/171027/3/a/59f34541a9fcf819a6442aff_800x800.jpg?imageMogr2/thumbnail/350x350!"
            alt
          />
        </div>
        <div class="swiper-slide">
          <img
            src="https://goods2.juancdn.com/goods/171027/3/a/59f34541a9fcf819a6442aff_800x800.jpg?imageMogr2/thumbnail/350x350!"
            alt
          />
        </div>
        <div class="swiper-slide">
          <img
            src="https://goods2.juancdn.com/goods/171027/3/a/59f34541a9fcf819a6442aff_800x800.jpg?imageMogr2/thumbnail/350x350!"
            alt
          />
        </div>
        <div class="swiper-slide">
          <img
            src="https://goods2.juancdn.com/goods/171027/3/a/59f34541a9fcf819a6442aff_800x800.jpg?imageMogr2/thumbnail/350x350!"
            alt
          />
        </div>
        <div class="swiper-slide">
          <img
            src="https://goods2.juancdn.com/goods/171027/3/a/59f34541a9fcf819a6442aff_800x800.jpg?imageMogr2/thumbnail/350x350!"
            alt
          />
        </div>
      </div>
    </div>

    <van-nav-bar title="查看店铺所有商品" />

    <van-tabs>
      <van-tab title="图文详情">
        <img
          src="https://goods2.juancdn.com/goods/190924/3/e/5d89baba33b08964054aa843_790x928.jpg"
          alt
        />
        <img
          src="https://goods5.juancdn.com/goods/190924/9/1/5d89bac0b6f8ea7b4560f42a_790x927.jpg"
          alt
        />
        <img
          src="https://goods6.juancdn.com/goods/190924/b/6/5d89bac633b08966505cf41d_790x928.jpg"
          alt
        />
        <img
          src="https://goods2.juancdn.com/goods/190924/3/b/5d89bacbb6f8ea78de09f607_790x928.jpg"
          alt
        />
        <img
          src="https://goods7.juancdn.com/goods/190924/d/d/5d89bad2b6f8ea7ee6516844_790x927.jpg"
          alt
        />
        <img
          src="https://goods4.juancdn.com/goods/190924/6/9/5d89badfb6f8ea7b7932cf09_790x927.jpg"
          alt
        />
      </van-tab>
      <van-tab title="商品参数">
        <div class="parameter">
          <span class="left-text">常用快递</span>
          <span>圆通速递、百世汇通</span>
          <div class="freight">
            <span class="left-text">运费</span>
            <span class="ora">全国包邮(偏远地区除外)</span>
          </div>
        </div>
      </van-tab>
      <van-tab title="咨询与售后">
        <div class="service">
          <h3>价格说明</h3>
          <p>1.商品的未划横线价格（显示如65）为卷皮销售价，在没有满减、额外折扣的情况下，该价格是交易成交价，是您最终决定是否购买商品的依据。</p>
          <p>2.商品的划横线价格为参考价，采集自品牌专柜标价、商品吊牌价或由品牌供应商提供的正品零售价；由于地区、时间的差异性和市场行情波动，品牌专柜标价、商品吊牌价可能会与您购物时展示的不一致，该价格仅供您参考。</p>
          <p>3商品的折扣比为卷皮销售价与参考价的对比（该值四舍五入后采小数点后1位，如24.9/65=0.38307=3.8折），该对比值仅供您参考，不作为结算基数。</p>
        </div>
      </van-tab>
    </van-tabs>

    <!-- 底部 -->
    <div class="box">
      <van-goods-action>
        <van-goods-action-icon
          icon="https://web.juanpi.com/static/media/home_dark.41f2a808.png"
          text="首页"
          @click="res"
        />
        <van-goods-action-icon
          icon="cart-o"
          :info="$store.getters['shoppingcart/num']"
          text="购物车"
          @click="$router.push('/shoppingcart')"
        />
        <van-goods-action-button type="warning" text="加入购物车" @click="onAddCartClicked" />
        <van-goods-action-button type="danger" text="立即购买" @click="onClickButton" />
      </van-goods-action>
    </div>
  </div>
</template>

<script>
import skuData from "../assets/text";
import { Toast } from "vant";
import Swiper from "swiper";
export default {
  data() {
    return {
      current: 0,
      skuData: skuData,
      showBase: false,
      showCustom: false,
      showStepper: false,
      showSoldout: false,
      closeOnClickOverlay: true,
      initialSku: {
        s1: "30349",
        s2: "1193",
        selectedNum: 3
      },
      customSkuValidator: () => "请选择xxx!"
    };
  },
  methods: {
    // 购买功能
    onClickButton() {
      // console.log(this.$store.commit);
      // this.$store.state.list.push({ bool: true, num: 1, price: 2.14, name: "棒棒糖" })
      if (this.$store.state.loginBool == false) {
        this.$router.push("/login");
        return;
      }
      this.$store.commit("shoppingcart/onClickButton", {
        bool: true,
        num: 1,
        price: this.$store.state.classify.Single[this.$route.query.index]
          .presentPrice,
        name: this.$store.state.classify.Single[this.$route.query.index]
          .describe,
        id: this.$store.state.classify.Single[this.$route.query.index].id,
        img: this.$store.state.classify.Single[this.$route.query.index].url
      });
      this.$router.push("/orderSubm");
    },
    // 轮播图
    onChange(index) {
      this.current = index;
    },
    // 加入购物车功能
    onAddCartClicked() {
      if (this.$store.state.loginBool == false) {
        this.$router.push("/login");
        return;
      }
      this.$store.commit("shoppingcart/onClickButton", {
        bool: true,
        num: 1,
        price: this.$store.state.classify.Single[this.$route.query.index]
          .presentPrice,
        name: this.$store.state.classify.Single[this.$route.query.index]
          .describe,
        id: this.$store.state.classify.Single[this.$route.query.index].id,
        img: this.$store.state.classify.Single[this.$route.query.index].url
      });
      Toast("添加成功，宝贝在购物车等亲");
    },
    // 跳转首页
    res() {
      this.$store.state.bool = true;
      this.$router.push("/home");
    },
    active() {
      if (this.$store.state.loginBool == false) {
        this.$router.push("/login");
        return;
      }
      this.$store.state.classify.Single[this.$route.query.index].active = !this
        .$store.state.classify.Single[this.$route.query.index].active;
    }
  },
  created() {
    // console.log("xxx");
    this.$store.state.bool = false;
  },
  // 生命周期钩子，最后销毁函数
  destroyed() {
    // 当组件被销毁时候，修改导航数据
    // this.$store.state.bool = true;
  },
  mounted() {
    // 注意：.swiper-container swiper中实例化对象第一值，是swiper显示最外面对象容器。这个类在重复使用swiper容易冲突，重新定义类名解决
    new Swiper(".swiperHomeNav", {
      slidesPerView: 4, //显示可视轮播图个数
      freeMode: true
    });
  }
};
</script>

<style lang="scss" scoped>
// .van-tabs--line .van-tabs__wrap {
//   width: 100%;
// }
.swiper-container {
  width: 100%;
